	module.exports = {"data":{"site":{"siteMetadata":{"title":"Therése Äleklint"}},"allMarkdownRemark":{"edges":[{"node":{"excerpt":"– Jag vet att du känner det, viskar han och kommer närmare.\nHans röst är som knoppriga frön, den glider in genom mitt öra, börjar gro inuti...","frontmatter":{"path":"/tva-bocker/","date":"16 September, 2017","title":"två böcker."},"html":"<blockquote>\n<p>– Jag vet att du känner det, viskar han och kommer närmare.\nHans röst är som knoppriga frön, den glider in genom mitt öra, börjar gro inuti mig. Det ska bli en djungel därinne, en utan logik men så jävla vacker.</p>\n<footer>Allt som blir kvar  – Sandra Beijer</footer>\n</blockquote>\n<p>Allt som blir kvar är en sådan bok som etsar sig fast. Griper tag, håller hårt och släpper inte taget. Språket och meningarna är så välformulerade att man vill hålla kvar vid varenda ord. Screenshota och spara på näthinnan. Boken är en berättelse om ett uppbrott, om en vänskapsrelation, om att tappa bort sig själv och samtidigt försöka leva och fånga äventyr.</p>\n<blockquote>\n<p>Hur kan jag vara någon som trots allt detta börjar sin resa på ett tåg? Tiden rör sig parallellt med rälsen, den verkar vara ett mått, inte på allt som ligger framför oss men ett mått på det förflutna, framtiden alltid vänd över sin egen axel, blickandes bakåt.</p>\n<footer>Tripprapporter – Tone Schunnesson</footer>\n</blockquote>\n<p>Okej, jag är en sucker för böcker med bra språk. I Tripprapporter rusar ord och meningar fram. Samtidigt som språket är poetiskt, känns det aldrig konstlat. Det är äkta och verkligt. Det är tankar, funderingar och drömmar. Det är ingen polerad yta, det är rått, kallt och vått.</p>"}}]}},"pathContext":{}}

